:root {
  --text-color: #2c3e50;
  --link-color: #bdc3c7;
  --bg-color: #f9f9f9;
  --border-color: #e1e1e1;
  --primary-color: #2980b9;

  --color-hero: #3498db;
  --color-primary: #34495e;
  --color-secondary: #ccd1d7;
  --color-inverse: #fff;
  --color-bg: #f8f8f8;

  --width-page: 68.75rem;

  --font-size-lg: 1.5rem;
  --font-size-md: 1.125rem;
  --font-size-rg: 1rem;
  --font-size-sm: 0.875rem;

  --font-semibold: 600;
  --font-regular: 400;

  --border-radius-md: 0.625rem;
  --border-radius-sm: 0.3125rem;

  --shadow-light: 0 4px 2px -2px rgba(0, 0, 0, 0.1);
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: "OpenSans";
  font-weight: 400;

  font-display: swap;
  src: url(/fonts/OpenSans-Regular.ttf) format("truetype");
}
@font-face {
  font-family: "OpenSans";
  font-weight: 600;

  font-display: swap;
  src: url(/fonts/OpenSans-SemiBold.ttf) format("truetype");
}

body {
  font-family: "Open Sans", "OpenSans", sans-serif;
  font-size: var(--font-size-rg);

  margin: 0;
  padding: 0;

  color: var(--color-primary);

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input[type="number"] {
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  margin: 0;

  -webkit-appearance: none;
}

input,
button {
  font: inherit;

  color: inherit;
}

a {
  text-decoration: none;

  color: inherit;
}

strong {
  font-weight: var(--font-semibold);
}

hr {
  width: 100%;

  opacity: .25;
  border: none;
  border-top: 1px solid var(--color-primary);
}

.heading {
  font-size: var(--font-size-lg);
  font-weight: var(--font-regular);

  margin: 0;

  text-align: center;
}

.subheading {
  font-size: var(--font-size-md);
  font-weight: var(--font-regular);

  margin: 0 0 1rem 0;

  text-align: center;
}

.btn {
  font-size: var(--font-size-rg);

  padding: .75rem 2rem;

  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;

  color: var(--color-inverse);
  border: 2px solid var(--color-primary);
  border-radius: var(--border-radius-sm);
  outline: none;
  background-color: var(--color-primary);
}

.btn:hover,
.btn:focus {
  border-color: var(--color-hero);
}

.card {
  width: 45%;
  min-width: 18.75rem;
  margin: 1rem;

  text-decoration: none;

  border-radius: var(--border-radius-md);
  background-color: var(--color-bg);
}

.card__img-container {
  height: 20rem;
  margin: 0;
  padding: 1rem;
}

.card__img {
  width: 100%;
  height: 100%;

  object-fit: contain;
}

.card__info {
  font-weight: var(--font-semibold);

  padding: 0 1.5rem 1.25rem;

  text-align: center;
}

.main-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;

  max-width: 45rem;
  margin: auto;
}

.navbar {
  position: fixed;
  z-index: 10;
  top: 0;

  width: 100%;
  height: 4.375rem;
  max-height: 10vh;

  color: var(--color-inverse);
  background-color: var(--color-primary);
}

.navbar__container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;

  max-width: var(--width-page);
  height: 100%;
  margin: 0 auto;
  padding: 0 2rem;
}

.navbar__header {
  font-size: var(--font-size-rg);
  font-weight: var(--font-semibold);

  text-transform: uppercase;
}

.navbar__link {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;

  height: 100%;
  padding: 0 1rem;

  text-decoration: none;

  color: var(--color-secondary);
}

.navbar__link--back {
  font-size: var(--font-size-sm);

  margin-left: auto;
}

.page-layout-container {
  width: 100%;
  max-width: var(--width-page);
  margin: 9rem auto 2rem auto;
}
.params-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;

  width: 100%;
  padding: 1.5rem;
}

.blueprint {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;

  width: 37.5rem;
}

.blueprint canvas {
  position: -webkit-sticky;
  position: sticky;
  top: 15vh;
}

.settings {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;

  width: 21.875rem;
  max-width: 100%;
}

.settings > *:nth-child(n + 3) {
  margin-top: 1.5rem;
}

.settings__input,
.settings label {
  font-size: 1rem;

  width: 100%;
}

.settings__input {
  border: none;
  border-bottom: 2px solid var(--border-color);
}

.settings__input:focus {
  border-bottom: 2px solid var(--primary-color);
  outline: none;
}

.settings__info {
  width: 100%;
}

.settings__add-to-chart {
  width: 100%;
  padding: 1rem 2rem;

  cursor: pointer;
  transition: all .2s;

  color: var(--primary-color);
  border: 2px solid var(--primary-color);
  border-radius: 5px;
  background-color: transparent;
}

.settings__add-to-chart:hover,
.settings__add-to-chart:focus {
  color: white;
  outline: none;
  background-color: var(--primary-color);
}

.range-input {
  font-size: 1rem;

  display: inline-block;

  padding: 0;

  text-align: center;

  border: none;
  border-bottom: 2px solid var(--border-color);
  border-radius: 0;
}

.range-input:focus {
  border-bottom-color: var(--primary-color);
  outline: none;
}

.window-options {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.window-options__caption {
  margin-right: 2rem;
}

.window-options__select {
  -webkit-order: 2;
          order: 2;
}

.window-options__checkbox {
  -webkit-flex-basis: 60%;
          flex-basis: 60%;

  color: var(--link-color);
}

.login__wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  min-height: 100vh;
}

.login__form {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;

  margin-bottom: 10rem;
  padding: 1rem 2rem;

  border-top: 4px solid var(--primary-color);
  border-radius: 6px;
  background-color: #eee;
  box-shadow: var(--shadow-light);
}

.login__form h5 {
  font-size: .875rem;

  margin: 0 0 .5rem 0;
}

.login__input {
  font-size: 1rem;

  padding: .2rem;

  transition: .15s all ease-in-out;

  border: 2px solid transparent;
  border-radius: 5px;
}

.login__input:focus {
  border-bottom-color: var(--primary-color);
  outline: none;
}

.login__btn {
  font-size: 1rem;

  width: 100%;
  margin: 2rem 0 1rem 0;
  padding: 1rem;

  transition: .15s all ease-in-out;

  color: #fff;
  border: 4px solid transparent;
  border-radius: 5px;
  background-color: rgb(87, 89, 95);
}

.login__btn:focus,
.login__btn:hover {
  border-bottom-color: var(--primary-color);
  outline: none;
}

.product-card {
  display: -webkit-flex;
  display: flex;

  padding: 0 20px;
  margin: 2rem 0;
}

.product-card__info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.product-card__header {
  display: -webkit-flex;
  display: flex;
}

.product-card__name {
  font-size: var(--font-size-rg);
  font-weight: var(--font-semibold);

  margin-right: 2rem;
}

.product-card__button {
  font-size: var(--font-size-sm);

  margin: 0 1rem;
  padding: 0;

  cursor: pointer;

  opacity: .75;
  border: none;
  background: none;
}

.product-card__button:hover,
.product-card__button:focus {
  opacity: 1;
  color: var(--color-hero);
  outline: none;
}

.product-card__params {
  font-size: var(--font-size-sm);
  line-height: 1.6;

  padding: 0 10px;

  list-style: none;
}


.product-card__final-info {
  margin-top: auto;
}

.final-info__count {
  margin-left: 3.125rem;
}

.product-card__image {
  max-width: 300px;
}

.cart-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
  margin: 2rem 20px;
}

.cart-footer__cost {
  margin-right: auto;
}

